<template name="order_classify_page">
	<view class="order_classify_page things_width_full">
        <!-- tabbar -->
        <cu-custom><block slot="content">我的订单</block></cu-custom>
		<scroll-view scroll-y class="things_width_full page">
            <view class="order_type_box">
                <view class="bg-img flex justify-start align-center things_width_full order_find order_find_car" style="background-image:url('./../../../../../static/images/publish/bg_car.png')" @tap="linkToCarOrder">
                    <image class="order_find_icon" src="./../../../../../static/images/publish/huoche.png" mode="widthFix" />
                    <view class="order_find_title">我的抢车单</view>
                </view>
                <view class="bg-img flex justify-start align-center things_width_full order_find order_find_freight" style="background-image:url('./../../../../../static/images/publish/bg_freight.png')" @tap="linkToFreightOrder">
                    <image class="order_find_icon" src="./../../../../../static/images/publish/huowu.png" mode="widthFix" />
                    <view class="order_find_title">我的抢货单</view>
                </view>
            </view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		name: "order",
		data() {
			return {
				
			};
        },
        methods: {
            /**
             * @description: 跳转到我的抢车单列表
             * @param {*} e
             * @return {*}
             * @author: gitxinghui
             */
            linkToCarOrder(e) {
                uni.navigateTo({
					url: `/pages/things/user/order/car/list/index`,
				});
            },
            /**
             * @description: 跳转到我的抢货单列表
             * @param {*} e
             * @return {*}
             * @author: gitxinghui
             */
            linkToFreightOrder(e) {
                uni.navigateTo({
					url: `/pages/things/user/order/freight/list/index`,
				});
            }
        },
		onShow() {
			console.log("success")
		}
	}
</script>

<style lang="scss" scoped>
	@import "./index.scss";
</style>
